.loader {
  width: 100px;
  height: 100px;
}
.loader svg {
  fill: var(--accent-color);
}
.loader svg:first-child {
  opacity: 0;
  transform: rotate(-45deg) translateY(20px);
  animation: leftPart 1.5s linear infinite;
}
.loader svg:last-child {
  opacity: 1;
  transform: rotate(-45deg) translateY(0);
  animation: rightPart 1.5s linear infinite;
}
@keyframes leftPart {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }
  50% {
    opacity: 1;
    transform: rotate(180deg);
  }
  100% {
    opacity: 0;
    transform: rotate(360deg);
  }
}
@keyframes rightPart {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }
  50% {
    opacity: 1;
    transform: rotate(-180deg);
  }
  100% {
    opacity: 0;
    transform: rotate(-360deg);
  }
}
