/* Модальные окна */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  opacity: 0;
}
.modal-overlay.is-opened {
  display: flex;
  animation: showOverlay 0.6s ease-in forwards;
}

.modal {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 90vw;
  max-width: 600px;
  height: fit-content;
  min-height: 200px;
  max-height: 90vh;
  background-color: #ffffff;
}
.modal__close {
  position: absolute;
  top: 0;
  right: 0;
}
.modal__content {
  margin-top: auto;
  overflow: hidden;
  overflow-y: auto;
  height: 100%;
}
.modal.is-active {
  animation: showModal 0.6s linear forwards;
}
@keyframes showModal {
  0% {
    opacity: 0;
    transform: translateY(105vw);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes showOverlay {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.modal__close {
  top: 30px;
  right: 30px;
}
@media (max-width: 768px) {
  .modal__close {
    top: 15px;
    right: 15px;
  }
}
.modal__close svg {
  fill: var(--white-color);
}
@media (max-width: 768px) {
  .modal__close svg {
    width: 32px;
    height: 32px;
  }
}

